#particles-about {
  position: absolute;
  height: 103.5vh;
  width: 100%; }

.about-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  min-height: 100vh; }

h3 {
  position: relative;
  @extend %small-bold;
  font-size: 30px;
  text-align: center;
  padding: 40px 0 10px;
  font-family: 'Open Sans', sans-serif;
  margin: 0px; }

.face-img {
  width: 100%;
  height: auto; }

.circular-portrait {
  width: 300px;
  height: 300px;
  overflow: hidden;
  border-radius: 50%; }

.blurb {
  position: relative;
  max-width: 920px;
  padding: 0 10px;
  margin: 20px auto;
  text-align: center;
  font-size: 24px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;

  p {
    margin-bottom: 0px;
    padding-bottom: 0px; } }
